.addtodo{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 10px;
}
.addtodo input{
    padding: 10px;
    margin: 10px;
    border: none;
    background-color: #fafafa;
    width: 400px;
}
.addtodo button{
    padding: 5px;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    font-size: 24px;
    margin-top: 10px;
    background-color: #48ac5c;
    color: white;
    border: none;
}