*{
    background-color: white;
    color: black;
    padding: 0;
    margin: 0;
}
.nav_title{
    font-size: 32px;
    font-weight: bold;
    letter-spacing: 2px;

}
.nav_title, .nav_links{
    text-align: center;
}
.nav_links{
    margin: 10px;
    display: flex;
    text-decoration: none;
    list-style: none;
    font-size: 18px;
    justify-content: space-around;
}
.nav_link{
    padding: 10px;
    background-color: #f8f4fc;
    border-radius: 10px;
    font-weight: 600;
    color: #985dec;
}
a{
    text-decoration: none;
    background-color: transparent;
}