.todo_container{
    margin: 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
}
.todo_container h4, .todo_container p, .todo_container button, .todo_container{
    background-color: #fff4f4;
}
.todo_container h4{
    width: 90%;
}
.todo_container p{
    margin: 5px 0 5px 0;
}
.todo_container button{
    padding: 10px;
    height: 40px;
    width: 40px;
    background-color: #D33636;
    border-radius: 50%;
    color: white;
    border: none;
    
}